/*header*/
.App-header {
  padding: 10px;
  width: 30em;
  margin: auto;
}
.head {
  padding: 1em 5px;
  -webkit-box-shadow: 0px 2px 4px 0px #888888;
          box-shadow: 0px 2px 4px 0px #888888;
  background: #733001;
  color: white;
}
.head>span {
  font-size: 1.5em;
}
.head>button {
  border-radius: 5px;
  margin: 0.5em;
  float: right;
}
.todoInfo {
  color: dimgray;
  background: lightgoldenrodyellow;
  padding: 10px 5px;
  -webkit-box-shadow: 3px 4px 7px 0px lightgrey;
          box-shadow: 3px 4px 7px 0px lightgrey;
}
.todoInfo>span {
  font-size: 1.2em;
}
.viewmore {
  border: none;
  color: dimgray;
  background: transparent;
  float: right;
}
.viewmore:hover {
  color: darkorange;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

/*todo input*/
input[type="text"], input[type="date"] , input[type="time"], textarea {
  background: transparent;
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  border: none;
  border-bottom: 1px solid lightgray;
}

.addinput {
  padding: 10px;
  margin: 5px;
}

.mrgn-left {
  margin-left: 20px;
}
.btn {
  border: none;
  background: #f1edc3;
  color: brown;
}
.center {
  display: block;
  margin: 0.5em auto
}
.btn:hover {
  background: #82b4b7;
  color: beige;
  -webkit-transition: linear 0.2s;
  -o-transition: linear 0.2s;
  transition: linear 0.2s;
}
.btn:disabled {
  background: lightgray;
  color: brown;
}
.txtar {
  resize: none;
}

/* todolist*/
ul {
  padding-left: 5px;
}
ul>li {
  list-style-type: none;
  margin: 8px 0px;
  -webkit-box-shadow: 2px 4px 8px 0px darkgrey;
          box-shadow: 2px 4px 8px 0px darkgrey;
}

/*todoitem*/
.todoitems {
  background: #fff2da;
  padding: 10px 0px;
}
.todoitems:hover {
  background: #ffe8a6;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s
}
.done {
  color: #20759e;
  background: #8feca8;
}
.done:hover {
  color: white;
  background: #558c92;;
}
.chkbox {
  float: left;
  margin-right: 0.5em;
}
.detailPanel {
  position: relative;
  top: 10px;
  padding: 5px 25px;
  background: rgba(255,255,255,0.5);
  -webkit-box-shadow: inset 0 10px 10px -10px slategrey;
          box-shadow: inset 0 10px 10px -10px slategrey;
}
.editPanel {
  margin: 0.5em;
  padding: 0.5em;
  background: rgba(255,255,255,0.6);
}
.inputCheck {
  cursor: pointer;
  float: left;
  margin: -2px 10px;
}
.inputCheck:hover {
  color: white;
}

/*btn*/
.tools {
  float: right;
  margin: 0px 5px;
}
.edit {
  background: #eadb65;
  color: black;
}
.del {
  background: #ff8383;
  color: black;
}

@media screen and (max-width: 480px) {
    .App-header {
      padding: 10px;
      width: 100%;
    }
}


